@import "./utils/vars";

@font-face {
  font-family: "#{$icomoon-font-family}";
  src: url("#{$icomoon-font-path}/#{$icomoon-font-family}.eot?9ggtyy");
  src: url("#{$icomoon-font-path}/#{$icomoon-font-family}.eot?9ggtyy#iefix") format("embedded-opentype"),
    url("#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?9ggtyy") format("truetype"),
    url("#{$icomoon-font-path}/#{$icomoon-font-family}.woff?9ggtyy") format("woff"),
    url("#{$icomoon-font-path}/#{$icomoon-font-family}.svg?9ggtyy##{$icomoon-font-family}") format("svg");
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="_icon-"]::before,
[class*=" _icon-"]::before {
  /* use !important to prevent issues with browser extensions */
  font-family: "#{$icomoon-font-family}" !important;
  // speak: never;`
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

._icon-logo {
  &:before {
    content: $_icon-logo;
  }
}

._icon-quote {
  &:before {
    content: $_icon-quote;
  }
}
