.vision {
  background-image: url("./../images/other/vision-shapes.svg");
  background-position: center;
  height: 690px;
  width: 100%;
  display: flex;
  align-items: center;

  @include onTablet7 {
    background-image: url("./../images/other/vision-shapes-narrow.svg");
  }

  @include onPhone {
    background-image: none;
  }

  &__wrapper {
    text-align: center;
    width: 754px;

    @include onTablet7 {
      width: fit-content;
    }
  }

  &__title {
    margin-bottom: 24px;
    @include titleH2;
    @include onPhone {
      font-size: 44px;
    }
  }

  &__text {
    margin-bottom: 64px;
    font-size: 24px;
    line-height: 150%;
    text-align: center;
    color: $fontColorText;

    @include onPhone {
      font-size: 22px;
    }
  }

  &__button {
    margin: 0 auto;
  }
}
